import React, { Component } from 'react';
import { Formik, FormikProps, Form, Field, ErrorMessage } from 'formik';
import {getHospitalSet,updatetodayPlan, getTodayPlan,updateTodayPlan} from '../Apis';
import { Alert } from 'reactstrap';
import * as Yup from 'yup';
import {
  Badge,
  Button,
  ButtonDropdown,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
// import { getTodayPlan } from '../Apis';



class UpdateTodayPlan extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name:[],
           
            visible:false,
         
            alternate_phone:'',
            gender:'',
            showForm:false,
            msgType:'success',
            massege:''
          
        };
        this.onDismiss = this.onDismiss.bind(this);
      }
    onDismiss() {
        this.setState({ visible: false });
      }
    handleSubmit = (values, { 
        props = this.props, 
        setSubmitting 
      }) => {
        this.updateTodayPlan(values,setSubmitting,this.props.match.params.id)
       
    }

    updateTodayPlan=async(values,setSubmitting,id)=>{
          const responseData= await updateTodayPlan(values,id);
          console.log(responseData,'response')
          if(responseData.data.message)
          {
              this.setState({ visible:true,massege:'Today Plan Update Failed '+responseData.message,msgType:'danger'})
              setSubmitting(false);
          }
          else{
              this.setState({ visible:true,msgType:'success',massege:'Today Plan Update Successfully'})
              setSubmitting(false);
              return;
          }
      }

    
      componentDidMount=async()=>{
            const responseData= await getHospitalSet();
            console.log(responseData,'response')
            if(responseData.status == 200){
                this.setState({hospitalSets:responseData.data.result})
            } else if (responseData.status == 404) {
                console.warn(responseData.data.message);
            } 

          
            const responseDatatodayPlan= await getTodayPlan(this.props.match.params.id);
            console.log(responseDatatodayPlan,'response')
            if(responseDatatodayPlan.status == 200){
                this.setState({
                
                    activity:responseDatatodayPlan.data.activity,
                  
                    hospital_id:responseDatatodayPlan.data.hospital_id,
                
                    created_by:responseDatatodayPlan.data.created_by,
                    showForm:true,
                    
                })
            } else if (responseDatatodayPlan.status == 404) {
                console.warn(responseDatatodayPlan.data.message);
            } 
            

        }
    render() {
        console.log(this.state.visible)
       
        return(
            <div className="animated fadeIn">
                <Row>
                <Col xs="2">
                </Col>
                    <Col xs="8">
                    <Card>
                        <CardHeader>
                            <strong>Update Today Plan</strong>
                        </CardHeader>
                        <CardBody> 
                            {this.state.showForm&&<Formik
                                    initialValues={{
                                        activity: this.state.activity,
                                    
                                        hospital_id: this.state.hospital_id,
                                        created_by:this.state.created_by,
                                        modified_by: localStorage.getItem('id'),
                                    }}
                            /*validate={values => {
                                let errors = {};
                                if (!values.email) {
                                  errors.email = 'Required';
                                } else if (
                                  !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                                ) {
                                  errors.email = 'Invalid email address';
                                }
                                return errors;
                              }}*/
                            // validationSchema={Yup.object().shape({
                            // email: Yup
                            //     .string()
                            //     .email()
                            //     .required(),
                            // password: Yup
                            //     .string()
                            //     .min(6)
                            //     .required(),
                            // confirm_password: Yup
                            //     .string()
                            //     .min(6)
                            //     .required(),
                            // phone:Yup
                            //     .number()
                            //     .required(),
                            // alternate_phone:Yup
                            //     .number()
                            //     .required(),
                            // })}
                            
                                    onSubmit={this.handleSubmit}
                                    render={formProps => {
                                        return(
                                        <Form className="form-horizontal">
                                            <FormGroup row>
                                                <Col md="3">
                                                    <Label htmlFor="text-input">Activity:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                    <Field 
                                                    type="text" 
                                                    name="activity" 
                                                    placeholder="Activity" 
                                                    id="text-input"
                                                    className="form-control"
                                                    /> 
                                                    <ErrorMessage name="activity" />
                                                </Col>                                        
                                            </FormGroup>
                                          
                                            
                                            <FormGroup row>
                                                <Col md="3">
                                                    <Label htmlFor="text-input">Hospital Name:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                    
                                                    <Field 
                                                    component="select" 
                                                    name="hospital_id" 
                                                    placeholder="Hospital Id" 
                                                    className="form-control"
                                                    >
                                                    {this.state.hospitalSets&&this.state.hospitalSets.map((hospital, index) =>
                                                    <option key={index} value={hospital.id}>{hospital.name}</option>
                                                    )}
                                                    </Field> 
                                                    <ErrorMessage name="short_name" />
                                                </Col>                                       
                                            </FormGroup>
                                      
                                            
                                          
                             
                                       
                                            <Col md="3" style={{float:'right'}}>
                                            <button 
                                                type="submit"
                                                className="btn btn-success btn-block custonbtn" 
                                                
                                                disabled={formProps.isSubmitting}>
                                                Update Today Plan
                                            </button>
                                            </Col>
                                            </Form>
                                );
                            }}

                            />}
                             <Alert color={this.state.msgType} style={{marginTop:65}}isOpen={this.state.visible} toggle={this.onDismiss}>
                                {this.state.massege}
                            </Alert>
                        </CardBody>
                    </Card>
                    </Col>
                </Row>
            </div>
            
        );
    }

}

export default UpdateTodayPlan;